.btn-xl {
    padding: 1.25rem 2.5rem;
  }
  
  .callout {
    padding: 15rem 0;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url("../assets/img/bg-callout.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .callout h2, .callout .h2 {
    font-size: 3.5rem;
    font-weight: 700;
    display: block;
    max-width: 30rem;
  }
  
  footer.footer {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
  footer.footer .social-link {
    display: block;
    text-decoration: none;
    height: 4rem;
    width: 4rem;
    line-height: 4.3rem;
    font-size: 1.5rem;
    background-color: #1D809F;
    transition: background-color 0.15s ease-in-out;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  }
  footer.footer .social-link:hover {
    background-color: #155d74;
    text-decoration: none;
  }
  
  .map {
    height: 30rem;
  }
  .map iframe {
    pointer-events: none;
    height: 100%;
    width: 100%;
    border: 0;
  }
  
  @media (max-width: 992px) {
    .fa-heart {
        display: block;
        margin: 0 auto;
    }
    .map {
      height: 75%;
    }
    img {       
        &.mx-auto {
          max-width: 3rem;
        }
      }
  }
  .masthead {
    min-height: 30rem;
    position: relative;
    display: table;
    width: 100%;
    height: auto;
    padding-top: 8rem;
    padding-bottom: 8rem;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url("../assets/img/bg-masthead.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .masthead h1, .masthead .h1 {
    font-size: 4rem;
    margin: 0;
    padding: 0;
  }
  @media (min-width: 992px) {
    .masthead {
      height: 100vh;
    }
    .masthead h1, .masthead .h1 {
      font-size: 5.5rem;
    }
    img {       
        &.mx-auto {
          max-width: 3.6rem;
        }
      }
  }

  .hero-img {
    background: transparent url("../../images/hero-img.jpg") no-repeat center;
    background-size: contain;
  }

  #iglesia {
    background: transparent url("../../images/hero-img.jpg") no-repeat 45% 70%;
    
  }
#fiesta {
    background: transparent url("../../images/fiesta.jpg") no-repeat center left;
    background-size: 100%;
}

  .gradient-time {
    background: $secondary;
    background: linear-gradient(90deg, $secondary 0%, rgb(199, 188, 72) 54%, $secondary 100%);
    width: 8rem;
    height: 8rem;
    border-radius: 10px;
    color: white;
    font-size: 2.2rem;
    line-height: 8rem;
    margin: 0 5px;
    display: inline-block;
    max-width: 22%;
  }

  .br-3 {
    border-right: 3px solid $secondary;
  }

